$mainColor: #9a2885;

@mixin bgGradient() {
  background-color: rgba(255, 255, 255, 0.3);
  font-weight: 400;
  color: #9a2885;
  border: none;
  border-radius: 14px;
  cursor: pointer;
  overflow: hidden;
  transition: 0.4s;
  position: relative;
  will-change: transform;

  &::after {
    content: "";
    position: absolute;
    top: -2%;
    left: -50%;
    width: 100%;
    height: 104%;
    transition: .6s clip-path, .5s left;
    clip-path: circle(0px at 50% 50%);
    background: rgb(2, 0, 36);
    background: linear-gradient(90deg, rgba(37, 149, 211, 1) 0%, rgba(154, 40, 133, 1) 100%);
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
    border: 2px solid #9a2885;
    border-radius: 14px;
  }
  & > * {
    transition: .3s;
  }
  &.slider::after {
    content: none;
  }
  span {
    pointer-events: none;
  }
  & > *:not(svg) {
    position: relative;
    z-index: 2;
  }
  &:hover, &.active {
    & > *:not(svg) {
      color: #fff;
    }
    &::after {
      left: 0;
      opacity: 1;
      clip-path: circle(110px at 50% 50%);
    }

    &.slider {
      background-color: rgba(255, 255, 255, 0.8);

      #slider-value {
        color: $mainColor;
      }

      &::after {
        transform: translateX(-150%);
      }
    }
  }
}

* {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  // font-family: 'Roboto', sans-serif;
  //font-family: system-ui, sans-serif;
  font-family: 'Fira Sans', sans-serif;
  text-align: center;
  font-weight: lighter;
  background-color: #fff;
  overflow: hidden;
}

#loader {
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 1;
  transition: 1s opacity;
  opacity: 0;

  &.show-loader {
    opacity: 1;
  }

  .svg-loader {
    pointer-events: none;
    position: absolute;
    height: 15%;
    top: 44%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .loading-bar {
    pointer-events: none;
    position: absolute;
    top: 52%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: 1s opacity;
    font-weight: 500;
    font-size: 20px;
    color: #fe2a52;
  }
}

#threeJS {
  width: 100%;
  height: 100%;

  .labels {
    left: 50%;
    transform: translateX(-50%);

    .label {
      text-align: left;
      width: 200px;
      background-color: rgba(0, 0, 0, 0.83);
      border: 3px solid #000;
      color: #fff;
      border-radius: 10px;
      padding: 3px 15px 7px;
      transition: 0.4s opacity;
      font-weight: 500;
      text-shadow: 0px 0px 2px rgb(255, 255, 255);
    }
  }
}

.progressBar {
  pointer-events: none;
  position: absolute;
  top: 57%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 1s opacity;
  font-size: 25px;
  color: #ffa09a;
  opacity: 0;

  &.show {
    opacity: 1;
  }
}

#panel {
  opacity: 0;

  #footer-menu {
    position: absolute;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    left: 0;
    width: 100%;
    height: 60px;
    padding: 0 7%;

    .buttons-container {

      display: flex;

      button.show {
        font-size: 18px;
        padding: 6px 20px;
        margin-right: 10px;
        -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0) inset;
        -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0) inset;
        box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0) inset;
        transition: 0.5s;
        filter: brightness(1);

        &:active {
          -webkit-box-shadow: 0px 5px 10px 2px rgba(124, 17, 62, 0.2) inset;
          -moz-box-shadow: 0px 5px 10px 2px rgba(124, 17, 62, 0.2) inset;
          box-shadow: 0px 5px 10px 2px rgba(124, 17, 62, 0.2) inset;
        }

        @include bgGradient;
      }
    }

    .slider-position {
      position: absolute;
      bottom: 65px;
      font-size: 18px;
    }

    .slider {
      display: flex;
      min-width: 300px;
      justify-content: center;
      align-items: center;
      padding: 8px 11px;

      .arrow {
        position: absolute;
        right: 4px;
        border-radius: 35px;
        transition: 0.2s;
        cursor: pointer;
        width: 25px;
        z-index: 2;

        path {
          fill: $mainColor;
          stroke: #9a2885;
        }

        &:hover {
          opacity: 0.8;
        }

        &:active {
          box-shadow: 0 0px 4px 1px $mainColor;
        }
      }

      #slider-value {
        padding: 0;
      }

      #left-arrow {
        transform: rotate(180deg);
        left: 4px;
        right: initial;
      }

      @include bgGradient;

      @media (max-width: 1448px) {
        position: static;
        transform: none;
      }
      @media (max-width: 977px) {
        position: absolute;
        transform: translate(-50%, -50%);
        top: -23px;
      }
    }

    #home {
      #home-icon {
        transition: 0.3s;
        cursor: pointer;
        padding: 6px 20px;
        @include bgGradient;
      }
    }
  }
}

#popUp {
  opacity: 0;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  align-content: flex-start;
  flex-wrap: wrap;
  background-color: rgba(0, 0, 0, 0);
  z-index: 4;
  transition: .3s;
  pointer-events: none;
  transform: scale(1.2);
  color: #fff;

  #rotate {
    margin-top: -17px;
    display: inline-block;
    margin-bottom: -8px;
  }

  h3 {
    width: 100%;
    margin-top: 20vh;
    color: #ffff;
    font-size: 30px;
    font-weight: 300;
    margin-bottom: 8vh;
  }

  .tab {
    width: 20%;
    margin: 0 20px;
    max-width: 250px;

    h4 {
      margin-bottom: 0;
      font-size: 25px;
    }

    p {
      font-size: 20px;
      font-weight: 400;
    }
  }

  #exitPopUp {
    position: absolute;
    right: 10%;
    top: 10%;
    width: 30px;
    height: 30px;

    svg {
      cursor: pointer;
      width: 100%;
      height: 100%;

      path {
        fill: #fff;
      }
    }
  }

  &.active {
    opacity: 1;
    background-color: rgba(0, 0, 0, 0.8);
    transform: scale(1);
    pointer-events: initial;
  }

  @media (hover: none) {
    flex-direction: column;
    .title {
      margin-top: 1vh;
      margin-bottom:4vh;
    }
    .tab {
      width: 100%;
      max-width: initial;
      text-align: center;
      margin: 0;
      padding: 0 10%;
      h4 {
        margin-top: 4px;
        margin-bottom: 0;
      }
      p  {
        margin-top: 4px;
        margin-bottom: 41px;
      }
    }
    #exitPopUp {
      top: 5%;
      right: 5%;
    }
  }
}

.mobile-drag {
  opacity: 0;
  pointer-events: none;
  transition: .3s;
  position: absolute;
  left: 38%;
  bottom: 130px;
  animation: leftRight 7s ease-in-out infinite;
  p {
    margin: 0;
    text-shadow: 1px 1px 8px rgba(0, 0, 0, 0.5);
    font-weight: 300;
    width: 100px;
    color: $mainColor;
  }

  &::after {
    pointer-events: none;
    content: "";
    position: absolute;
    width: 60%;
    height: 60%;
    background-color: rgba(0, 0, 0, 0.3);
    transform: translate(-50%, -135.8%);
    filter: blur(15px);
    border-radius: 95px;
    z-index: -1;
  }
  &.active {
    opacity: 1;
  }
}

@keyframes leftRight {
  0% {
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
  }
  50% {
    -webkit-transform: translateX(50%);
    transform: translateX(50%);
  }
  100% {
    -webkit-transform: translateX(50%);
    transform: translateX(-50%);
  }
}

@media (max-width: 704px) {
  #threeJS .labels .label {

  }
  #panel {
    #footer-menu {
      padding: 0 6%;

      .slider {
        position: absolute;
        transform: translate(-50%, -50%);
        top: -23px;
      }

      .buttons-container {
        position: relative;
        z-index: 3;

        #list-but {
          position: absolute;
          bottom: 100%;
          opacity: 0;
          pointer-events: none;
          transition: 0.3s;
        }
          &.active #list-but{
            button.show {
              background-color: #fff;
            }
          }

        button.show {
          min-width: 200px;

          //&.active {
          //}
        }

        &.active {
          #list-but {
            opacity: 1;
            pointer-events: initial;

            button.show {
              opacity: 1;
              background-color: rgba(255,255,255,0.8);
            }
          }

          & + .slider-position, & + .slider-position * {
            pointer-events: none;
            opacity: 0;
          }
        }
      }

    }
  }

}
